import React from 'react'
import { Link } from 'gatsby'

import Layout from '../components/layout'

const IndexPage = () => (
  <Layout>
    <h2>Philosophy in the modern age</h2>
    <p>Welcome! I'm Miguel!</p>
    <p>
    	If you came accross this page, chances are you found the link from my portfolio out clicked it
    	out of curiosity. This site captures my reasons for learning philosophy everyday and how 
    	it may benefit you as well.  
    </p>  
    <p>
    	I hope you find value in stoicism the same way I did.
    </p> 
    <p> 
    	But why is there a need to learn for philosophy anyways? I'm totally comfortable with my life now!
    </p>
	<h2>Why study stoicism?</h2>
	<div>
		Have you ever realized that we live now in a very noisy world. Want to hear some news that
		gets us going everyday?	
		<ul>
			<li><a href='https://www.nytimes.com/2018/09/05/opinion/trump-white-house-anonymous-resistance.html'>I Am Part of the Resistance Inside the Trump Administration</a></li>
			<li><a href='https://www.express.co.uk/sport/othersport/1022649/NBA-news-Kawhi-Leonard-awkward-laugh-Toronto-Raptors-media-day-WATCH'>NBA news: Kawhi Leonard MOCKED for awkward laugh at Toronto Raptors media day - WATCH</a></li>
			<li><a href='https://people.com/style/kim-kardashian-flaunts-curves-pink-dress-nyc/'>Kim Kardashian Flaunts Curves During NYC Outing with Husband </a></li>
		</ul>
		<div>
				I applaud you for not clicking the above topics. You belong to 1% of the world who could 
				control themselves. But I have another set of questions..<br/><br/>
			<ul>
				<li>At work how many choices did you took without thinking about your manager's instructions?</li>
				<li>Did you buy something this week because of television or facebook advertisements?</li>
				<li>How much time did you spend browsing instagram today?</li>
			</ul>	 
		</div>
		<br/>
		<p>
			<b>Have you ever realized how much time you WASTED to consume news, social media and other people's opinion?</b>
			<br/><br/>
			We live in a very noisy world and it causes us to ultimately become unproductive most of the time.
			The time that we could have spent more for our family and creating meaningful projects are most of the time <b>WASTED</b>.
			<br/><br/>
			Beyond wasted effort and time, there is a lot more to stoicism. Stoicsm is about conquering ourselves through fortitude
			perseverance and suffering, for these are the only thing that we can absolutely improve or destroy.
			Stoicism focuses on how to influence the things that we can only control. 
			<br/><br/>	
		</p>
	</div>
	<h2>A brief history of Stoicism</h2>
	<h3>Zeno of Citium</h3>
	<div>	
		<img src="data:image/jpeg;base64," alt='zeno'/>
		<p> 
			Stoicism is a school of Hellenistic philosophy founded by Zeno of Citium in Athens 
			in the early 3rd century BC. It was heavily influenced by certain teachings of Socrates, 
			while Stoic physics are largely drawn from the teachings of the philosopher Heraclitus. 
			Stoicism is predominantly a philosophy of personal ethics informed by its system of 
			logic and its views on the natural world. According to its teachings, as social beings,
			the path to happiness for humans is found in accepting this moment as it presents itself,
			by not allowing ourselves to be controlled by our desire for pleasure or our fear of pain,
			by using our minds to understand the world around us and to do our part in nature's plan,
			and by working together and treating others fairly and justly. <a href='https://en.wikipedia.org/wiki/Stoicism'>More info</a>
		</p>
	</div>
	<h3>Marcus Aurelius</h3>
    <div>
    	<img src='https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQB6UjOfK7Db76DG0uxOHz_qLrSdFlJWXlLNfJZLLjDurq6vWbHzA' alt='marcus aurelius'/>
	    <p>
	    	While on campaign between 170 and 180, Marcus Aurelius wrote his Meditations in Greek
	     	as a source for his own guidance and self-improvement. The original title of this work,
	     	if it had one, is unknown. "Meditations," as well as other titles, including "To Himself,"
	     	were adopted later. He had a logical mind and his notes were representative of Stoic 
	     	philosophy and spirituality. <a href='https://en.wikipedia.org/wiki/Marcus_Aurelius'>More info</a>
	    </p>
	</div>   
	<h3>Lucius Annaeus Seneca</h3>
	<div style={{ justifyContent: 'center' }}>
	    <img src='data:image/jpeg;base64,' alt='seneca'/>
	    <p>
		     Seneca generally employs a pointed rhetorical style in his prose.
		     His writings focus on traditional themes of Stoic philosophy. 
		     The universe is governed for the best by a rational providence, and this has to be reconciled with adversity. 
		     Seneca regards philosophy as a balm for the wounds of life.
		     The destructive passions, especially anger and grief, must be uprooted, although sometimes
		     he offers advice for moderating them according to reason.He discusses the relative merits 
		     of the contemplative life and the active life, and he considers it important to confront 
		     one's own mortality and be able to face death. One must be willing to practice poverty and
		     use wealth properly, and he writes about favours, clemency, the importance of friendship, and
		     the need to benefit others. <a href='https://en.wikipedia.org/wiki/Seneca_the_Younger'>More info</a>
	     </p>
    </div>
	<div>
	    <h3>Epictetus</h3>
	    <img src='data:image/jpeg;base64,' alt='epictetus' />
	    <p>
	    	Prisoner of war James Stockdale receiving the Medal of Honor from American president
	    	Gerald Ford; Stockdale was able to retain his sanity during capture by relying on the
	    	philosophy of Epictetus James Stockdale. The philosophy of Epictetus is well known in 
	    	the U.S. military through the writings and example of James Stockdale, the 1992 vice 
	    	presidential candidate of Ross Perot and a fighter pilot who was shot down while serving
	    	in the Vietnam War. He was introduced to the works of Epictetus while at Stanford
	    	University. In Courage under Fire: Testing Epictetus's Doctrines in a Laboratory of
	    	Human Behavior (1993), Stockdale credits Epictetus with helping him endure his seven 
	    	and a half years in captivity, which included torture and four years in solitary 
	    	confinement. When he was shot down, he reportedly said to himself "I'm leaving the
	    	world of technology and entering the world of Epictetus!" as he bailed out. <a href='https://en.wikipedia.org/wiki/Epictetus#Philosophy'>More info..</a>	
	    </p>
	</div>    
    <h2>Where to go from here..</h2>
		<p>
	    	I stumbled on this philosophy two years ago and have adopted it eversince. 
	    	It is now a pillar to my ability to build projects, create content 
	    	and analyze data impartially. It enabled me to learn from my mistakes. 
	    	It helped me seek what really matters most for me and that is to - unravel my hidden potential
	    	and use it for the betterment of the world. It may sound too altruistic but nevertheless it
	    	is the force that binds me now as I look forward to creating more interesting projects like 
	    	this and share it to the world. 
    	</p>
    	<p>
    		<b>I wish to learn more where do I go from here Miguel?</b>
    		<br/><br/>
    		Philosophy is to be applied throughout our lifetime, this holds true for my own experience and the likes of 
    		Marcus, Seneca and Epictetus. We are constantly bombarded with noise and problems of the modern society.  
    		Moving forward, my advice is to read and act. Read everyday and apply what you have learned from the materials you consume. These 
    		lives that the great stoics should not be wasted away. Click the link below for my materials to learn
    		Stoicism. 

    	</p>	

    <Link to="/page-2/">More on Stoicsm</Link>
  </Layout>
)

export default IndexPage

